*
{
  background-color: black;
}

h3
{
  margin: 40vh 0vw 20px 40vw;
  color: white;
}

.box 
{
  display: flex;
  background-color: white;
  height: 50px;
  width: 180px;
  margin: 10px 0px 0px 42vw;
  border-radius: 10px;
}

button
{
  color: black;
  background-color: rgb(255, 255, 255);
  height: 50px;
  width: 60px;
  border: none;
  font-weight: bold;
  font-size: 26px;
  cursor: pointer;
}

.plus
{
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}

.ct
{
  width: 60px;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 25px;
  font-weight: bold;
}

.minus
{
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
}

.reset
{
  font-size: 18px;
  margin: 20px 0px 0px 680px;
  height: 35px;
  border-radius: 10px;
  width: 100px;
  cursor: pointer;
}